@import url(https://fonts.googleapis.com/css?family=Roboto);
body {
  font-family: 'Roboto', sans-serif;
}

html {
  background-color: #b161fb;
}

.accountForm {
  box-shadow: 0px 0px 100px -20px rgba(25,29,50,0.75);
  border-radius: 15px;
  width: 450px;
  height: 550px;
  margin: 50px;
  background-color: rgba(250, 250, 255, 0.5);
}

.accountFormHeader {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 140px;
  margin-bottom: 10px;
  font-size: 2.5em;
  color: #FAFAFF;
  border-radius: 15px 15px 0 0;
  /* Permalink - use to edit and share this gradient: http://colorzilla.com/gradient-editor/#9d37fc+8,6600c8+100 */
background: rgb(157,55,252); /* Old browsers */ /* FF3.6-15 */
background: -webkit-linear-gradient(45deg, rgba(157,55,252,0.8) 8%,rgba(102,0,200,0.8) 100%); /* Chrome10-25,Safari5.1-6 */
background: linear-gradient(45deg, rgba(157,55,252,0.8) 8%,rgba(102,0,200,0.8) 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#9d37fc', endColorstr='#6600c8',GradientType=1 ); /* IE6-9 fallback on horizontal gradient */
}

.accountFormBody {
  display: flex;
  flex-direction: column;
  align-items: space-around;
  padding: 25px;
}

.accountForm p {
  color: #666;
  font-size: 0.9em;
}

.accountForm p:hover {
  text-decoration: underline;
  cursor: pointer;
  color: #191D32;
}

/* Change Autocomplete styles in Chrome*/
input:-webkit-autofill,
input:-webkit-autofill:hover,
input:-webkit-autofill:focus
textarea:-webkit-autofill,
textarea:-webkit-autofill:hover
textarea:-webkit-autofill:focus,
select:-webkit-autofill,
select:-webkit-autofill:hover,
select:-webkit-autofill:focus {
  border-radius: 5px;
  -webkit-text-fill-color: #191D32;
  -webkit-box-shadow: 0 0 0px 1000px #3E9132 inset;
  -webkit-transition: background-color 5000s ease-in-out 0s;
  transition: background-color 5000s ease-in-out 0s;
}


@media only screen and (max-width: 1200px) {
  .accountForm {
    width: 550px;
  }
}

@media only screen and (max-width: 750px) {
  .accountForm {
    width: 100vw;
    margin: 50px 0;
    border-radius: 0;
  }
  .accountFormHeader {
    border-radius: 0; 
  }
}

.jumbotron {
  display: flex;
  align-items: center;
  justify-content: space-around;
  height: 1000px;
}

.welcomeText {
  text-align: left;
  font-size: 2em;
  color: #191D32;
  margin-left: 25px;
  text-shadow: 4px 5px 12px rgba(25,29,50,0.35);
}

.welcomeText span {
  color: #9D37FC;
  text-shadow: 4px 5px 12px rgba(157, 55, 252,0.35);
}

.welcomeText img {
  height: 250px;
  width: 250px;
  /* background-color: #9D37FC; */
  padding: 25px;
  border-radius: 50px;
  margin-top: 0;
  background: rgb(157,55,252); /* Old browsers */ /* FF3.6-15 */
  background: -webkit-linear-gradient(45deg, rgba(157,55,252,1) 8%,rgba(102,0,200,1) 100%); /* Chrome10-25,Safari5.1-6 */
  background: linear-gradient(45deg, rgba(157,55,252,1) 8%,rgba(102,0,200,1) 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#9d37fc', endColorstr='#6600c8',GradientType=1 ); /* IE6-9 fallback on horizontal gradient */
}

#descriptionSectionOne {
  background-color: #9D37FC;
  box-shadow: 0px 0px 75px 0px rgba(25,29,50,0.75);
}

#descriptionSectionOne .descriptionBody {
  text-align: left;
}

#descriptionSectionTwo {
  background-color: #191D32;
}

#descriptionSectionTwo .descriptionBody {
  text-align: right;
}

#descriptionSectionThree {
  background-color: #9D37FC;
}

#descriptionSectionThree .descriptionBody {
  text-align: left;
}

.descriptionBody {
  display: flex;
  justify-content: space-around;
  align-items: center;
}

.description div {
  margin: 25px;
}

.description section {
  height: 750px;
  color: #fafaff;
  padding: 25px;
  opacity: 0.95;
  font-size: 1.5em;
}

.description section h2 {
  font-size: 2.5em;
  margin: 10px 0;
}

.description section strong {
  font-size: 1.1em;
}

@media only screen and (max-width: 1200px) {
  .jumbotron {
    display: flex;
    flex-direction: column;
    height: 1750px;
  }
  .welcomeText {
    text-align: center;
    margin-left: 0;
  }
}

@media only screen and (max-width: 1000px) {
  .descriptionGraphic img {
    height: 250px;
    width: 250px;
  }
}

@media only screen and (max-width: 750px) {
  .descriptionBody {
    flex-direction: column;
  }
  .description div {
    margin: 0;
  }
  .description section {
    height: 900px;
    font-size: 1.25em;
  }
}

footer {
  display: flex;
  justify-content: space-around;
  align-items: center;
  background-color: #191D32;
  opacity: 0.95;
  height: 250px;
  color: #FAFAFF;
  font-size: 1.25em;
  box-shadow: 0px 0px 75px 0px rgba(25,29,50,0.75);
}

.footerContact {
  display: flex;
  flex-direction: column;
  border-left: 2px solid rgba(250,250,255, 0.5);
  padding: 0 150px;
}

.footerContact a {
  color: #d8d8dd;
  text-decoration: none;
}

.footerContact a:hover {
  color: #FAFAFF;
  text-decoration: underline;
}

@media only screen and (max-width: 1200px) {
  .footerContact {
    padding: 0 50px;
  }
}


@media only screen and (max-width: 800px) {
  .footerContact {
    padding: 0 25px; 
  }
}

.App {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  text-align: center;
  min-height: 100vh;
  background-image: url(/Revision/static/media/memphis-colorful.8b33b229.png);
  /* Background pattern from Toptal Subtle Patterns */
  background-attachment: fixed;
  overflow-x: hidden;
}

